// @flow

import React, {Component} from 'react';
import LocalStorage from 'local-storage';
import Immutable from 'immutable';

import './App.css';
import {
  CompositeDecorator,
  ContentState,
  Editor,
  EditorState,
  Modifier,
  RichUtils,
  convertToRaw,
  convertFromRaw,
} from 'draft-js';

const LS_KEY = 'state002';

const typeToColor = {
  keyword: 'red',
  numerical: '#FEDF6B',
  typename: '#FEDF6B',
  label: '#9C49B6',
  const: '#FEDF6B',
  string: '#FEDF6B',
  char: '#FEDF6B',
  regex: '#FEDF6B',
  paren: '#444',
  name: '#9EE062',
  recurse: '#67B3DD',
  param: '#FDA947',
  module: '#FDA947',
  comment: 'grey',
  operator: '#67B3DD',
  normal: 'white',
  malformed: '#880000',
};

const compositeDecorator = new CompositeDecorator([
  {
    strategy: findWithRegex(/\bfn\b/g),
    component: applyStyle({color: typeToColor.keyword}),
  },
  {
    strategy: findWithRegex(/\$\w+/g),
    component: applyStyle({color: typeToColor.name}),
  },
]);

const recover = () => {
  const existing = LocalStorage.get(LS_KEY);
  return existing ? convertFromRaw(existing) : ContentState.createFromText('');
};

const setStyle = (editorState, type) => {
  const selection = editorState.getSelection();
  const currentStyle = editorState.getCurrentInlineStyle();
  let nextEditorState = currentStyle.reduce(
    (state, type) => RichUtils.toggleInlineStyle(state, type),
    editorState,
  );
  // If the color is being toggled on, apply it.
  nextEditorState = RichUtils.toggleInlineStyle(nextEditorState, type);
  // Commit the change
  nextEditorState = EditorState.push(
    EditorState.setInlineStyleOverride(
      editorState,
      nextEditorState.getInlineStyleOverride(),
    ),
    nextEditorState.getCurrentContent(),
    'change-inline-style',
  );
  return nextEditorState;
};

class MyEditor extends React.Component<{||}, {editorState: EditorState}> {
  editor: ?Editor;
  currentType: ?string;

  state = {
    editorState: EditorState.createWithContent(
      recover(),
      // (compositeDecorator: any),
    ),
  };
  onChange = editorState => {
    const selection = editorState.getSelection();
    const preservedStyle =
      this.currentType != null &&
      editorState.getCurrentInlineStyle().first() !== this.currentType
        ? setStyle(editorState, this.currentType)
        : editorState;
    LocalStorage.set(LS_KEY, convertToRaw(preservedStyle.getCurrentContent()));
    this.setState({editorState: preservedStyle});
  };

  onToggle = toggledType => {
    this.currentType = toggledType === this.currentType ? null : toggledType;
    nullthrows(this.editor).focus();
    if (this.currentType == null) {
      return;
    }
    setTimeout(() => {
      this.onChange(setStyle(this.state.editorState, toggledType));
    }, 0);
  };

  handleKeyCommand = (command: string) => {};

  render() {
    return (
      <div>
        <ColorControls
          currentStyle={this.currentType}
          onToggle={this.onToggle}
        />
        <div
          style={{
            backgroundColor: '#222',
            color: 'white',
            fontFamily: 'Monaco, Consolas',
            fontSize: 13,
            padding: 20,
            height: '100%',
          }}>
          <Editor
            customStyleMap={Immutable.Seq.Keyed(typeToColor)
              .map(color => ({color}))
              .toObject()}
            editorState={this.state.editorState}
            handleKeyCommand={this.handleKeyCommand}
            ref={ref => {
              this.editor = ref;
            }}
            onChange={this.onChange}
          />
        </div>
      </div>
    );
  }
}

const ColorControls = props => (
  <div style={{}}>
    {Object.entries(typeToColor).map(([type, color]) => (
      <StyleButton
        active={props.currentStyle === type}
        color={color}
        key={type}
        type={type}
        onToggle={props.onToggle}
      />
    ))}
  </div>
);

const StyleButton = props => (
  <span
    style={{
      borderWidth: 1,
      borderColor: 'white',
      borderStyle: props.active ? 'solid' : 'none',
      display: 'inline-block',
      width: 20,
      height: 20,
      backgroundColor: props.color,
      marginRight: 5,
    }}
    onClick={() => props.onToggle(props.type)}
  />
);

class App extends Component<{||}> {
  render() {
    return (
      <div
        style={{
          backgroundColor: '#151515',
          color: '#aaa',
          fontSize: 16,
          padding: 20,
          height: '100%',
        }}>
        <div style={{paddingBottom: 20}}>Edit below:</div>
        <MyEditor />
      </div>
    );
  }
}

function findWithRegex(regex) {
  return (contentBlock, callback, contentState) => {
    const text = contentBlock.getText();
    let matchArr, start;
    while ((matchArr = regex.exec(text)) !== null) {
      start = matchArr.index;
      callback(start, start + matchArr[0].length);
    }
  };
}

function applyStyle(style) {
  return props => (
    <span {...props} style={style}>
      {props.children}
    </span>
  );
}

const nullthrows = <T>(x: ?T): T => {
  if (x != null) {
    return x;
  }
  throw new Error('unexpected null');
};

export default App;



